	import uniGrid from "@/components/uni-grid/uni-grid.vue";
	import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue";
    import {mapState} from 'vuex';  
	
    export default {
		components: {
			uniGrid,uniGridItem
		},
		data(){
			return {
				
			}
		},
		onLoad(){
		},
        computed: {
			...mapState(['hasLogin','userInfo'])
		},
        methods: {
			navTo(url){
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				uni.navigateTo({  
					url
				})  
			},
        }  
    }  
